<template><section><h1>PrimitiveCollection</h1>
<p><code v-pre>vc-collection-primitive</code> 组件用于加载常规图元集合。通常用的是 <code v-pre>vc-viewer</code> 初始化得到的 <code v-pre>Viewer</code> 实例自带的一个成员属性 <code v-pre>Scene.primitives</code>。它可作为一切图元的父组件，如有需要也可以作为子集嵌套一层或多层再加载到 <code v-pre>Scene.primitives</code>。</p>
<h2>示例</h2>
<h3>加载普通图元集合</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive @click="clicked" :show="show">
          <vc-collection-primitive-billboard :billboards="billboards1"></vc-collection-primitive-billboard>
          <vc-collection-primitive>
            <vc-collection-primitive-billboard :billboards="billboards2"></vc-collection-primitive-billboard>
          </vc-collection-primitive>
        </vc-collection-primitive>
        <vc-collection-primitive>
          <vc-primitive-model @click="clicked" :url="url" :modelMatrix="modelMatrix" :scale="10000" :minimumPixelSize="128" :maximumScale="200000">
          </vc-primitive-model>
        </vc-collection-primitive>
      </vc-viewer>
      <div class="demo-tool">
        <span>{{ show ? '隐藏' : '显示' }}</span>
        <md-switch v-model="show"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span> <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive-billboard</span> <span class="hljs-attr">:billboards</span>=<span class="hljs-string">"billboards1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-primitive-billboard</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive-billboard</span> <span class="hljs-attr">:billboards</span>=<span class="hljs-string">"billboards2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-primitive-billboard</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-primitive</span>&gt;</span>
