      <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"clicked"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-model</span> <span class="hljs-attr">:url</span>=<span class="hljs-string">"url"</span> <span class="hljs-attr">:modelMatrix</span>=<span class="hljs-string">"modelMatrix"</span> <span class="hljs-attr">:scale</span>=<span class="hljs-string">"10000"</span> <span class="hljs-attr">:minimumPixelSize</span>=<span class="hljs-string">"128"</span> <span class="hljs-attr">:maximumScale</span>=<span class="hljs-string">"200000"</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-model</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-primitive</span>&gt;</span>
