          billboard1.position = { <span class="hljs-attr">lng</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">40</span> + <span class="hljs-number">85</span>, <span class="hljs-attr">lat</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">30</span> + <span class="hljs-number">21</span> }
          billboard1.image = <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>
          billboard1.scale = <span class="hljs-number">0.1</span>
          billboards1.push(billboard1)

          <span class="hljs-keyword">let</span> billboard2 = {}
          billboard2.position = { <span class="hljs-attr">lng</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">40</span> + <span class="hljs-number">85</span>, <span class="hljs-attr">lat</span>: <span class="hljs-built_in">Math</span>.random() * <span class="hljs-number">30</span> + <span class="hljs-number">21</span> }
          billboard2.image = <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>
          billboard2.scale = <span class="hljs-number">0.15</span>
          billboards2.push(billboard2)
        }
        <span class="hljs-keyword">this</span>.billboards1 = billboards1
        <span class="hljs-keyword">this</span>.billboards2 = billboards2
        <span class="hljs-keyword">this</span>.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(<span class="hljs-number">105</span>, <span class="hljs-number">38</span>, <span class="hljs-number">10000</span>))
      },
      clicked(e) {
        <span class="hljs-built_in">console</span>.log(e)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
